            .wh-main {
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            .workout-display {
                display: flex;
                justify-content: center;
                align-items: top;
                width: 100%;
            }

            .prev-workouts {
                display: flex;
                flex: 1 1 55%;
                padding: 50px;
                margin: 50px;
                flex-direction: column;
                justify-content: top;
                align-items: center;
                font-size: 20px;
            }

            .prev-workouts button {
                font-size: 20px;
                width: 20%;
                border-radius: 20px;
            }

            .prev-workouts ul {
                margin: 10px 10px;
                padding-left: 0px;
            }

            #previous-workouts {
                margin-left: 30px;
                margin: 5px 30px;
                padding-left: 0px;
                padding-right: 0px;
                font-size: 18px;
            }

            table {
                background-color: rgb(0, 255, 0);
                text-align: center;
                width: 85%;
                font-size: 22px;
                font-family: 'Saira';
                border-collapse: collapse;
                border: 1.5px solid rgba(255, 255, 255, 0.5);
            }

            table th {
            background-color: rgba(255, 255, 255, 0.3);
            padding: 10px;
            text-align: center;
            font-weight: bold;
            border: 1.5px solid rgba(255, 255, 255, 0.5);
            }

            table td {
            padding: 10px;
            text-align: center;
            border: 1.5px solid rgba(255, 255, 255, 0.5);
            }


            table tr:hover {
            background-color: rgb(50, 50, 50, 0.3);
            }

            .muscle-group {
                outline: 2px solid red;
                background-color: red;
                fill: red
            }

            map {
                position: relative;
                z-index: 1;
            }

            area {
                position: absolute;
                z-index: 2;
                background-color: rgba(255, 0, 0, 0.3); /* Red color with 0.3 opacity */
                width: 150px;
                height: 100px;
                visibility: visible;
                opacity: 1;
            }

            .image-wrapper {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 50%;
            }

            .back-image-wrapper {
                position: absolute;
                top: 0px;
                left: 400px;
                height: 100%;
                width: 50%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;

            }

            .highlight {
                position: absolute;
                border: 2px solid red;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                box-sizing: border box;
                pointer-events: none;
            }

            .human-body {
                flex: 1;
                padding: 20px;
                text-align: center;
                margin: 20px;
            }

            .image-container {
                display: flex;
                top: 25px;
                position: relative;
                width: 750px;
                height: 750px;
                align-items: center;
            }

            .table-row-highlighted {
                background-color: rgb(300, 300, 300, 0.6);    
            }

